body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.app-header {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  text-align: center;
  background-image: linear-gradient(black,blueviolet);
  color: white;
  justify-content: center;
  align-items: center;
}

.heading-text {
  color: aqua;
  font-size: 42px;
  font-weight: bolder;
  text-align: center;
  margin: 0 50px 0px 50px;
}

.square- {
  min-width: 100px;
  min-height: 100px;
  font-size: 64px;
  text-align: center;
  color: blueviolet;
  font-weight: bolder;
  padding: 30px;
}
.square-0 {
  min-width: 100px;
  min-height: 100px;
  font-size: 64px;
  text-align: center;
  color: red;
  font-weight: bolder;
  padding: 30px;
}
.square-X {
  min-width: 100px;
  min-height: 100px;
  font-size: 64px;
  text-align: center;
  color: blueviolet;

  font-weight: bolder;
  padding: 30px;
}



.fc-aqua {
  color: #1CDBF5;
}

.fc-white {
  color: white;
}

.fw-600 {
  font-weight: bolder;
}

.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.jc-center {
  justify-content: center;
}

.b-bottom-right {
  border-right: 15px solid brown;
  border-bottom: 15px solid brown;
}

.b-bottom {
  border-bottom: 15px solid brown;
}

.b-right {
  border-right: 15px solid brown;
}

.clear-button {
  width: max-content;
  margin: 40px auto 5px auto;
  font-size: medium;
  background-color: aquamarine;
  border-radius: 10px;
}
#chance{
  font-size: medium;
  color:magenta;
}

@media only screen and (max-width: 600px) {
  .heading-text {
      color: white;
      font-size: 30px;
      font-weight: bolder;
      text-align: center;
      /*margin: 60px 10px;*/
  }

  .square- {
      min-width: 80px;
      min-height: 80px;
      font-size: 40px;
      font-weight: bolder;
      padding: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
  }
  .square-X {
    min-width: 80px;
    min-height: 80px;
    font-size: 40px;
    font-weight: bolder;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}
#chance{
  font-size: small;
  color: chocolate;
}
.square-0 {
  min-width: 80px;
  min-height: 80px;
  font-size: 40px;
  font-weight: bolder;
  padding: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}


  .b-bottom-right {
      border-right: 10px solid brown;
      border-bottom: 10px solid brown;
  }

  .b-bottom {
      border-bottom: 10px solid brown;
  }

  .b-right {
      border-right: 10px solid brown;
  }

  .clear-button {
      font-size: large;
      width: max-content;
      margin: 40px auto 5px auto;
      font-size: medium;
      background-color: aquamarine
  }
}